import React, { Component } from 'react'
import { Grid, Cell, List, ListItem, ListItemContent } from 'react-mdl';


export default class Contact extends Component {
    render() {
        return (
         <div className="contact-body">
            <Grid className="contact-grid">
                <Cell col={6}>
                 <h2>Kelvin King</h2>
                 <img
                  src="https://cdn.pixabay.com/photo/2016/08/20/05/38/avatar-1606916_960_720.png"
                  alt="Profile"
                  style={{ height: '250px' }}
                 />
                <p style={{width: '75%', margin: 'auto', paddingTop: '1em', textAlign: 'left'}}>
                    Lorem Ipsum is simply dummy text of the printing 
                    and typesetting industry. Lorem Ipsum has been the 
                    industry's standard dummy text ever since the 1500s, when an unknown printer 
                    took a galley of type and scrambled it to make a 
                    type specimen book. It has survived not only five centuries
                 </p>
                </Cell>
                <Cell col={6}>
                 <h2>Contact Me</h2>

                 <hr/>

                 <div className="contact-list">
                  <List>
                    <ListItem>
                        <ListItemContent style={{fontSize: '30px', fontFamily: 'Anton'}}>
                          <i class="fa fa-phone-square" aria-hidden="true" />
                          (123) 456-7890
                        </ListItemContent>
                    </ListItem>

                    <ListItem>
                       <ListItemContent style={{fontSize: '30px', fontFamily: 'Anton'}}>
                          <i class="fa fa-fax" aria-hidden="true" />
                          (123) 456-7890
                       </ListItemContent>
                    </ListItem>

                    <ListItem>
                       <ListItemContent style={{fontSize: '30px', fontFamily: 'Anton'}}>
                          <i class="fa fa-envelope" aria-hidden="true" />
                          someone@gmail.com
                         </ListItemContent>
                      </ListItem>
                    <ListItem>
                       <ListItemContent style={{fontSize: '30px', fontFamily: 'Anton'}}>
                         <i class="fa fa-skype" aria-hidden="true" />
                         MySkypeID
                        </ListItemContent>
                     </ListItem>
                   </List>
                 </div>
               </Cell>
            </Grid>
        </div>
        )
    }
}
