import React, { Component } from 'react';
import { Grid, Cell } from 'react-mdl';
import Education from './Education';
import Experience from './Experience';
import Skills from './Skills';

class Resume extends Component {
    render() {
        return (
            <div>
                <Grid>
                    <Cell col={4}>
                        <div style={{textAlign: 'center'}}>
                            <img
                                src="https://cdn.pixabay.com/photo/2016/08/20/05/38/avatar-1606916_960_720.png"
                                alt="resume"
                                style={{height: '200px'}}
                            />
                        </div>
                            <h2 style={{paddingTop: '15rem'}}>King Kelvin</h2>
                                <h4 style={{color: 'grey'}}>Programmer</h4>
                               <hr style={{borderTop: '3px solid #833fb2', width: '100%'}}/>
                            <p>
                                Lorem Ipsum is simply dummy text of the printing 
                                and typesetting industry. Lorem Ipsum has been the 
                                industry's standard dummy text ever since the 1500s, when an unknown printer 
                                took a galley of type and scrambled it to make a 
                                type specimen book. It has survived not only five centuries
                            </p>
                             <hr style={{borderTop: '3px solid #833fb2', width: '100%'}}/>
                                <h5>Address</h5>
                                <p>I Hacker Way, 9876</p>
                                <h5>Phone</h5>
                                <p>(123) 456-7890</p>
                                <h5>Email</h5>
                                <p>someone@gmail.com</p>
                                <h5>Web</h5>
                                <a href="https://www.mywebsite.com"><p>Here</p></a>
                                <hr style={{borderTop: '3px solid #833fb2', width: '100%'}}/>


                    </Cell>
                    <Cell className="resume-right-col" col={8}>

                        <h2>Education</h2>

                        <Education
                            startYear={2010}
                            endYear={2014}
                            schoolName="My University"
                            schoolDescription="Lorem Ipsum is simply dummy text of the printing 
                            and typesetting industry. Lorem Ipsum has been the 
                            industry's standard dummy text ever since the 1500s, when an unknown printer 
                            took a galley of type and scrambled it to make a"
                        />
                        <Education
                            startYear={2002}
                            endYear={2014}
                            schoolName="My Second University"
                            schoolDescription="Lorem Ipsum is simply dummy text of the printing 
                            and typesetting industry. Lorem Ipsum has been the 
                            industry's standard dummy text ever since the 1500s, when an unknown printer 
                            took a galley of type and scrambled it to make a"
                        />
                        <hr style={{borderTop: '3px solid #e22947'}} />

                        <h2>Experience</h2>

                        <Experience />
                        <Experience
                            startYear={2010}
                            endYear={2014}
                            jobName="React Developer"
                            jobDescription="Lorem Ipsum is simply dummy text of the printing 
                            and typesetting industry. Lorem Ipsum has been the 
                            industry's standard dummy text ever since the 1500s, when an unknown printer 
                            took a galley of type and scrambled it to make a"
                        />
                        <Experience
                            startYear={2002}
                            endYear={2014}
                            jobName="Angular Developer"
                            jobDescription="Lorem Ipsum is simply dummy text of the printing 
                            and typesetting industry. Lorem Ipsum has been the 
                            industry's standard dummy text ever since the 1500s, when an unknown printer 
                            took a galley of type and scrambled it to make a"
                        />

                        <hr style={{borderTop: '3px solid #e22947'}} />

                        <h2>Skills</h2>

                        <Skills
                            skill="Javascript"
                            progress={100}
                         />
                        <Skills
                            skill="HTML/CSS"
                            progress={90}
                        />
                        <Skills
                            skill="React"
                            progress={45}
                        />
                        <Skills
                            skill="NodeJS"
                            progress={60}
                        />
                        <Skills
                            skill="MongoDB"
                            progress={70}
                        />
                    </Cell>
                </Grid>
            </div>
        );
    }
}

export default Resume;