import React, { Component } from 'react';
import { Tabs, Tab, Grid, Cell, Card, CardTitle, CardText, CardActions, Button, CardMenu, IconButton } from 'react-mdl';


class Projects extends Component {

    constructor(props){
        super(props);
        this.state = { activeTab: 0 };
    }
        toggleCategories(){
            if(this.state.activeTab === 0){
                return(
               <div style={{width: '100%', margin: 'auto' }}>
                <Grid>
                 <Cell col={4}>
                   <Card shadow={5} style={{minWidth: '30%', margin: 'auto' }}>
                    <CardMenu style={{color: '#fff'}}>
                     <a href="http://www.github.com"><IconButton name="share" /></a>
                    </CardMenu>
                    <CardTitle style={{color: '#fff', height: '176px', background: 'url(https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTb9q8QN50EBYl2J6sgGwbiHrYyd3j77A7qXXZ1P3Or_ib9KNl2) center / cover'}}>
                        React Project #1
                    </CardTitle>
                    <CardText>
                     Lorem ipsum dolor sit amet, consectetur adipiscing elit. Mauris sagittis pellentesque lacus eleifend lacinia...
                    </CardText>
                    <CardActions border>
                        <Button colored>Github</Button>
                        <Button colored>CodePen</Button>
                        <Button colored>LiveDemo</Button>
                    </CardActions>
                    </Card>                 
                 </Cell>
                 <Cell col={4}>
                  <Card shadow={5} style={{minWidth: '30%', margin: 'auto' }}>
                    <CardMenu style={{color: '#fff'}}>
                      <a href="http://www.github.com"><IconButton name="share" /></a>
                    </CardMenu>
                    <CardTitle style={{color: '#fff', height: '176px', background: 'url(https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTb9q8QN50EBYl2J6sgGwbiHrYyd3j77A7qXXZ1P3Or_ib9KNl2) center / cover'}}>
                      React Project #2
                    </CardTitle>
                    <CardText>
                     Lorem ipsum dolor sit amet, consectetur adipiscing elit. Mauris sagittis pellentesque lacus eleifend lacinia...
                    </CardText>
                    <CardActions border>
                        <Button colored>Github</Button>
                        <Button colored>CodePen</Button>
                        <Button colored>LiveDemo</Button>
                    </CardActions>
                  </Card>
                 </Cell>
                 <Cell col={4}>
                  <Card shadow={5} style={{minWidth: '30%', margin: 'auto' }}>
                    <CardMenu style={{color: '#fff'}}>
                     <a href="http://www.github.com"><IconButton name="share" /></a>
                    </CardMenu>
                    <CardTitle style={{color: '#fff', height: '176px', background: 'url(https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTb9q8QN50EBYl2J6sgGwbiHrYyd3j77A7qXXZ1P3Or_ib9KNl2) center / cover'}}>
                     React Project #3
                    </CardTitle>
                    <CardText>
                      Lorem ipsum dolor sit amet, consectetur adipiscing elit. Mauris sagittis pellentesque lacus eleifend lacinia...
                    </CardText>
                    <CardActions border>
                        <Button colored>Github</Button>
                        <Button colored>CodePen</Button>
                        <Button colored>LiveDemo</Button>
                    </CardActions>
                  </Card>
                 </Cell>
                </Grid>
            </div>
                )
            }else if( this.state.activeTab === 1) {
                 return (
                    <div><h1>This is Angular</h1></div>
                 )   
            } else if( this.state.activeTab === 2) {
                return (
                   <div><h1>This is VueJS</h1></div>
                )   
           } else {
               return(
                <div><h1>This is MongoDB</h1></div>
               )
           }
    }


    render() {
        return (
            <div className="category-tabs">
                <Tabs activeTab={this.state.activeTab} onChange={(tabId) => this.setState({activeTab: tabId})} ripple>
                    <Tab>React</Tab>
                    <Tab>Angular</Tab>
                    <Tab>VueJs</Tab>
                    <Tab>MongoDB</Tab>
                </Tabs>
                <section>
                  <div className="content">{ this.toggleCategories() }</div>
                </section>
            </div>
        );
    }
}

export default Projects;